@import url(https://fonts.googleapis.com/css?family=Montserrat|Raleway:400,500);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.navbar {
  height: 64px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem; }
  .navbar__logo__link {
    text-decoration: unset;
    color: #434543;
    font-family: "Raleway", sans-serif;
    line-height: normal; }
    .navbar__logo__link__name {
      font-size: 18px;
      font-weight: 500; }
    .navbar__logo__link__profession {
      font-size: 13px; }
  .navbar__collapse--items {
    display: inline; }

.listedItems {
  margin: 0;
  display: inline; }
  .listedItems__item {
    display: inline; }
    .listedItems__item__btnCollapse {
      all: unset;
      display: none;
      font-size: 1.5rem;
      color: black;
      margin-right: 1rem; }
    .listedItems__item__link {
      margin-left: 1rem;
      margin-right: 1rem;
      color: unset;
      text-decoration: unset;
      font-family: "Raleway", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: normal;
      color: black; }

@media only screen and (max-width: 768px) {
  .navbar--responsive {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 10%;
    padding-right: 10%;
    height: unset; }
  .listedItems__item__btnCollapse {
    display: inline;
    margin-top: 1rem;
    margin-bottom: 1rem; }
  .listedItems__item__link {
    display: none; }
    .listedItems__item__link--responsive {
      display: block;
      margin: 0;
      margin-bottom: 1rem; }
  .listedItems--responsive {
    display: flex;
    flex-direction: column; } }

.navBorder {
  border: 0;
  height: 1px;
  background: #333;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(50%, rgba(0, 0, 0, 0.75)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.75) 50%, rgba(0, 0, 0, 0) 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba(0, 0, 0, 0)), color-stop(50%, rgba(0, 0, 0, 0.75)), color-stop(100%, rgba(0, 0, 0, 0)));
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.75) 50%, rgba(0, 0, 0, 0) 100%); }

